// root
:root {
  $--font-main: "ApercuProMono";
  font: size 16px $--font-main;
}

// text line height
$--line-height-simple: 1rem;
$--line-height-quart: 1.25rem;
$--line-height-double: 2rem;
$--line-height-triple: 3rem;
$--line-height-quad: 4rem;

// margin
$--margin-0: 0px;
$--margin-10: 10px;
$--margin-20: 20px;
$--margin-simple: 1rem;
$--margin-double: 2rem;
$--margin-triple: 3rem;
$--margin-quad: 4rem;

// main colors
$--color-purple: #5114de;
$--color-pink: #ffe0dc;
$--color-green: #7d938a;
$--color-orange: rgb(240, 97, 30);
$--color-black: black;
$--color-white: white;
$--color-yellow: #fff83d;


$--color-background: #1f1e1e;

// border
$--border-background: #cbcbcb;

//font
$--font-main: "ApercuProMono";
$--font-secondary: "ITC Odyssee";
$--font-menu: "TIMMONS NY 2.0";

$--font-size-1: 1rem;
$--font-size-2: 2rem;
$--font-size-4: 4rem;
$--font-size-5: 5rem;
$--font-size-7: 7rem;
$--font-size-8: 8rem;

.menu_main {
  position: absolute;
  bottom: 100%;
  top: 32px;
  height: 40px;
  z-index: 99;

}
.menu_main2 {
  position: fixed;
  bottom: 100%;
  top: 20px;
  height: 40px;
  z-index: 99;

}


