.menu_mobile {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center; /* pour le menu en mode mobile */
  position: sticky;
  width: 100%;
  height: 100vh;
  z-index: 0;
  top: 0px;
  padding: 0px;
}

.sticky_mobile {
  display: flex; /* pour le menu en mode mobile */
  position: fixed;
  width: 100%;
  height: 25px;
  top: 0px;
  z-index: 0;
}

.menu {
  display: flex;
  width: 100%;
  justify-content: space-between;
  max-width: 70vw;
  margin-bottom: 0px;
  font-family: $--font-main;
  font-style: normal;
  font-weight: 100;
}

.cat {
  color: $--color-pink;
  line-height: 50px;
  height: 50px;
}
.cat:hover {
  color: $--color-pink;
  cursor: crosshair;
}
.cat2 {
  color: $--color-pink;
}

.cat-y {
  color: $--color-yellow;
}

.menu2 {
  display: flex; /* pour le menu en mode mobile */
  width: 100%;
  max-width: 90vw;
  margin-bottom: $--margin-0;
  font-family: $--font-main;
  font-style: normal;
  font-weight: 100;
  flex-direction: column;
  align-items: center;
}

/* pour le menu mobile et animation ouverture */
.load_main {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.loader_element1 {
  position: absolute;
  width: 101vw;
  height: 120vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url("../../gif/raw_gif/bg_mobile_min.gif");
  background-size: cover;
  background-blend-mode: exclusion;
  background-color: rgb(255, 255, 255);
  z-index: 1;
  animation: clip1 1s cubic-bezier(0.92, 0.52, 0.21, 0.89) 1 reverse forwards;
  animation-delay: 0s;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  overflow: hidden;
}

.texte_loader_center {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  align-content: center;
  align-items: center;
  font-size: $--font-size-7;
  color: rgba(255, 255, 255, 0.85);
  font-family: $--font-menu, Arial, Helvetica, sans-serif;
  margin: 0px;
  padding: 0px;
  transform: translateX(300px);
  animation-name: opacity, transform;
  animation-duration: 1s, 2s;
  animation-timing-function: cubic-bezier(0.77, 0.34, 0.98, 0.57),
    cubic-bezier(0.48, 0.37, 0.09, 0.5);
  animation-fill-mode: forwards;
  animation-delay: 0.1s;
}

@keyframes opacity {
  100% {
    opacity: 1;
  }
}

@keyframes transform {
  100% {
    transform: translateX(0px);
  }
}

.loader_element2 {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 101vw;
  height: 120vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $--color-green;
  background-size: cover;
  box-sizing: border-box;
  border: 0px solid rgb(71, 71, 71);
  z-index: 2;
  animation-name: clip1, borderr;
  animation-duration: 1s, 0.5s;
  animation-timing-function: cubic-bezier(0.92, 0.52, 0.21, 0.89), ease-out;
  animation-fill-mode: forwards, forwards;
  animation-delay: 0.9s, 1.7s;
  animation-direction: reverse, normal;
  animation-iteration-count: 1, 1;
  clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
  overflow-x: hidden;
  overflow-y: hidden;
}

@keyframes clip1 {
  0% {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }
  10% {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 10% 100%);
  }
  100% {
    clip-path: polygon(100% 0%, 100% 0%, 100% 100%, 100% 100%);
  }
}

@keyframes borderr {
  100% {
    border: 10px solid $--color-white;
  }
}

.item_center {
  display: inline-flex;
  box-sizing: border-box;
  width: 100%;
  height: 10vh;
  margin-top: 1.5rem;
  align-content: flex-end;
  justify-content: center;
  align-items: center;
  font-size: $--font-size-5;
  color: rgba(255, 255, 255, 0.7);
  font-family: $--font-menu, Arial, Helvetica, sans-serif;
  padding: 0px;
  flex-wrap: nowrap;
  flex-direction: row-reverse;
}

.categorie {
  transform: translateX(300px);
}

@keyframes opacity {
  100% {
    opacity: 1;
  }
}
@keyframes transform {
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}

.categorie.wrap1 {
